import Vue from 'vue'
import App from './App.vue'
import VueResource from 'vue-resource'
import VeeValidate, { Validator } from 'vee-validate';
import es from './requires/es';


import 'normalize.css'
import 'animate.css'


Validator.extend('all', {
  getMessage: (field, args) => 'All fields need to be checked',
  validate: function (value, args) {
    return Object
      .keys(value)
      .every(key => !!value[key])
  }
})

Vue.use(VeeValidate);

Validator.localize('es', es);
Validator.localize('es', {
  custom: {
    terms: {
      required: 'Debes aceptar la autorización.'
    }
  }
});

Vue.use(VueResource);

new Vue({
  render: h => h(App),
}).$mount('#app')
